/* Teams-specific styles */
.teams-app {
  min-height: 100vh;
  background: var(--background-color, #f3f2f1);
  color: var(--text-color, #323130);
}

.teams-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: #f3f2f1;
}

.loading-spinner {
  padding: 20px;
  font-size: 16px;
  color: #605e5c;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% { opacity: 0.6; }
  50% { opacity: 1; }
  100% { opacity: 0.6; }
}

/* Teams theme support */
.teams-app[data-theme="dark"] {
  --background-color: #201f1e;
  --text-color: #ffffff;
  --surface-color: #292827;
  --border-color: #484644;
}

.teams-app[data-theme="contrast"] {
  --background-color: #000000;
  --text-color: #ffffff;
  --surface-color: #1f1f1f;
  --border-color: #ffffff;
}

/* Adjust existing components for Teams */
.teams-app .App {
  padding: 8px;
  margin: 0;
}

.teams-app .app-container {
  max-width: 100%;
  margin: 0;
  gap: 12px;
}

.teams-app .status-panel,
.teams-app .chat-interface {
  border: 1px solid var(--border-color, #e1dfdd);
  border-radius: 4px;
  background: var(--surface-color, #ffffff);
}

/* Teams-compatible scrollbars */
.teams-app ::-webkit-scrollbar {
  width: 8px;
}

.teams-app ::-webkit-scrollbar-track {
  background: var(--background-color, #f3f2f1);
}

.teams-app ::-webkit-scrollbar-thumb {
  background: var(--border-color, #c8c6c4);
  border-radius: 4px;
}

.teams-app ::-webkit-scrollbar-thumb:hover {
  background: #a19f9d;
}

/* Responsive adjustments for Teams */
@media (max-width: 768px) {
  .teams-app .app-container {
    flex-direction: column;
  }
  
  .teams-app .status-panel {
    order: 2;
  }
  
  .teams-app .chat-interface {
    order: 1;
  }
}
